<template>
  <div class="userResult">
    <Overview
      v-if="overviewData !== null"
      :chart-data="overviewData"
      :highlight="highlight"
      :quiz-total-score="quizTotalScore"
    />
    <Individual
      v-if="answerGroup"
      :chartdata="answerGroup.answerList"
      :answer-group="answerGroup"
      :overall-quiz-data="overallQuizData"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import Overview from '@/components/Overview.vue';
import Individual from '@/components/Individual.vue';
import AnswerGroupApi from '@/libs/AnswerGroupApi';
import Utils from '@/libs/Utility';

export default {
  components: {
    Overview,
    Individual,
  },
  data() {
    return {
      overviewData: null,
      highlight: null, // {label: stirng, x: number, y: number}
      peopleLength: 0,
      answerGroup: null,
      overallQuizData: [],
      quizTotalScore: 0,
      quizGroupId: null, // 有給值 但未使用
      personalQuizData: [], // 無值 未使用
    };
  },
  computed: {
    individualInfo() { // 無值 未使用
      return this.$store.state.individualInfo;
    },
    currentLang() {
      return this.$store.state.currentLang;
    },
  },
  watch: {
    async currentLang() {
      await this.reDrawChart();
    },
  },
  created() {
  },
  async mounted() {
    await this.reDrawChart();
    const { lang } = this.$route.query;
    // 這邊會從query拿language 而這個query是在後端發結果message給user時就設定好了(根據user profile language 設定)
    if (lang) {
      this.$i18n.locale = lang;
    }

    // temp fix, set lang to zh for all users
    // this.$i18n.locale = 'us';
    this.$store.commit('updateCurrentLang', lang);
    // ----------- 以上強迫學生無論原本環境，一開始只能看到中文

    this.$store.commit('updateLoading', true);
    try {
      await this.fetchData();
    } catch (error) {
      this.$router.push({ name: 'NoData' });
      console.error(error);
    } finally {
      this.$store.commit('updateLoading', false);
    }
  },
  methods: {
    async reDrawChart() {
      this.$store.commit('updateLoading', true);
      try {
        await this.fetchData();
      } catch (error) {
        this.$router.push({ name: 'NoData' });
        console.error(error);
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    async fetchData() {
      const { userId, quizGroupId, ansGroupId } = this.$route.query;
      this.quizGroupId = quizGroupId;
      const { answerGroupId } = this.$route.params;
      if (!answerGroupId) {
        throw new Error('未提供必要參數');
      }

      const TIME_LIMIT = 600; // 10 min

      this.answerGroup = await AnswerGroupApi.retrieve(answerGroupId);
      this.quizTotalScore = this.answerGroup.totalScore;

      this.highlight = {
        label: this.$t('我的表現').toString(),
        // y: this.answerGroup.correctRate * this.answerGroup.sourceQuizGroupSize,
        y: this.answerGroup.scoreGot,
        x: Utils.formatFloat(this.answerGroup.timeSpent),
      };
      try {
        // 圖表資料來源
        const dataSet = await AnswerGroupApi.getOverviewV2(this.answerGroup.sourceQuizGroupId);
        this.overviewData = dataSet.map((data) => ({
          x: data.timeSpent > TIME_LIMIT ? TIME_LIMIT : Utils.formatFloat(data.timeSpent), // do not display if spent more than 250 seconds
          y: data.scoreGot,
        }));
        this.peopleLength = this.overviewData.length;

        // console.log(this.answerGroup.sourceQuizGroupId); // [題組本身]ID ?
        // 處理每一題的全體成績和作答時間
        const overallQuizData = await AnswerGroupApi.getAverageDataList(this.answerGroup.sourceQuizGroupId, null);
        this.overallQuizData = overallQuizData.map((data) => ({
          timeSpent: Utils.formatFloat(data.timeSpent),
          scoreAvg: Utils.formatFloat(data.scoreAvg),
        }));
        // console.log('每題的全體成績和作答時間', this.overallQuizData);

        // console.log(this.answerGroup.quizEventId); // 該[測驗]ID ? personal???
        // const personalQuizData = await AnswerGroupApi.getAverageDataList(null, this.answerGroup.quizEventId);
        // this.personalQuizData = personalQuizData.map((data) => ({
        //   timeSpent: Utils.formatFloat(data.timeSpent),
        //   scoreAvg: Utils.formatFloat(data.scoreAvg),
        // }));
        // console.log('personal???', this.personalQuizData);
      } catch (error) {
        if (error.response && error.response.status === 404) {
          this.$notify({
            type: 'warning',
            text: this.$t('無法載入分析圖表...').toString(),
          });
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>

</style>
